#store-page {
  position: relative;
  top: 80px;
  background-image: linear-gradient(var(--purple4), var(--purple2));
}

.store-grid-item {
  background: var(--purple1);
  color: var(--purple5);
  border-radius: 5px;
  padding: 20px !important;
}

.store-grid-item h3 {
  color: var(--purple3);
  font-size: 25px;
  text-align: center;
}

.items-grid-cont {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: 80px;
  justify-content: center;
  position: relative;
  width: 100%;
  padding: 20px 20px;
  padding-top: 80px;
  margin-bottom: 80px;
  background: var(--purple1);
  color: var(--purple5);
  border-radius: 5px;
}

.items-grid-cont h3 {
  color: var(--purple4);
  font-size: 25px;
  position: absolute;
  padding: 20px;
}

.inner-grid-cont {
  width: 100%;
  display: grid;
  gap: 50px;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
}

#store-info {
  margin-bottom: 40px;
}

#extra-space-store {
  height: 1px;
}

.info-block {
  display: inline-block !important;
}

#info-title {
  padding-right: 10px;
  margin-right: 10px;
}

.rating {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
}

.rating > input {
  display: none;
}

.rating > label {
  position: relative;
  width: 1em;
  font-size: 25px;
  color: var(--yellow3);
}

.rating > label::before {
  content: "\2605";
  position: absolute;
  opacity: 0;
}

.rating > input:checked ~ label:before {
  opacity: 1;
}

#backsplash-div {
  height: 150px;
  width: auto;
  background-image: url("../assets/backsplash_placeholder.png");
  background-repeat: repeat-x;
}

#item-icon {
  width: 75px;
  margin-left: 40px;
  margin-top: 10px;
  margin-bottom: -10px;
}

#rating-wrapper {
  float: left;
}

#categories-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
}

#categories {
  width: 100%;
  display: inline-block !important;
  margin-bottom: 40px;
}

#store-bio-text {
  display: inline-block;
}

#store-info-text {
  display: inline-block;
}

#extra-space-store-home {
  height: 50px;
}

.store-grid-container {
  padding: 80px;
}

.cat {
  background: var(--yellow2);
  color: var(--yellow4);
  margin: 10px;
  padding: 5px;
  text-align: center;
  border-radius: 5px;
}

.cat:hover {
  background: var(--yellow3);
  color: var(--yellow5);
}

.cat:active {
  background: var(--yellow4);
  color: var(--yellow1);
}

@media only screen and (max-width: 500px) {
  #store-page {
    font-size: 10px !important;
  }

  #store-page h3 {
    font-size: 15px !important;
  }

  #store-page h2 {
    font-size: 20px !important;
  }

  #item-icon {
    width: 30px;
  }

  .inner-grid-cont {
    display: block;
    justify-content: center;
    position: relative;
  }

  .cards {
    width: 500px;
    margin-bottom: 10px;
  }

  .store-grid-container {
    padding: 10px;
  }
}

/* Adds padding from top so categories are not cut off by the header 
 * when navigating using the category buttons */
a.anchor {
  display: block;
  position: relative;
  top: -100px;
  visibility: hidden;
}
